/* Modal styles */
.loginModal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; /* Ensure it's on top of everything */
  }
  
  .loginModalContent {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    width: 400px; /* Wider for better spacing */
    position: relative;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* More pronounced shadow */
  }
  
  .closeButton {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 24px; /* Larger close button */
    cursor: pointer;
    color: #777; /* Muted color */
    transition: color 0.3s ease;
  }
  
  .closeButton:hover {
    color: #333; /* Darken on hover */
  }
  
  .loginModalContent h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  .formGroup {
    margin-bottom: 20px; /* More spacing */
  }
  
  .formGroup label {
    display: block;
    margin-bottom: 8px;
    font-weight: 500;
    color: #555;
  }
  
  .formGroup input {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 6px; /* Slightly rounded corners */
    font-size: 16px;
    transition: border-color 0.3s ease;
  }
  
  .formGroup input:focus {
    outline: none;
    border-color: #3498db; /* Highlight on focus */
  }
  
  .loginModalContent button {
    background-color: #3498db;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .loginModalContent button:hover {
    background-color: #2980b9;
  }
  
  /* Optional: Add some animation */
  .loginModalContent {
    animation: fadeIn 0.4s ease-in-out;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  